@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: rgb(109, 109, 109);
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.custom-color{
  color: rgb(38, 151, 104);
}

.custom-bg {
  background-color: rgb(38, 151, 104);
  color: white;
}
.message-content{
  position: relative;
  display: inline-block;
  margin: 10px;
  padding: 10px;
  border-radius: 20px;
}

.message-content:hover .delete-btn{
  opacity: 1;
}

.delete-btn {
  cursor: pointer;
  color: red;
  position: absolute;
  top: 0;
  right: 10px;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
  opacity: 0;
}

.message-item.right {
  display: flex;
  justify-content: flex-end;
}

.message-item.left  .message-content{
  background-color: rgb(231, 231, 231);
}

.message-item.right .message-content{
  background-color: rgb(38, 151, 104);
  color: white;
}

.date{
  font-size: 11px;
}



.messages-container{
  margin-top: 15px;
  margin-bottom: 15px;
  max-height: 70vh;
  overflow-y: auto;
}
.container {
  background-color: white;
  min-height: 100vh;
}
